body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
width:70%;
margin:0 auto;
background: grey;
color: white;
font-weight: bold;
}

#AddTodoForm{
 margin:0 auto;
 margin-right:30%;
 padding-top:2%;
}

#AddToDo{
    margin: 0 auto; 
}

input{
    border:green;
    border-style: dashed;
    line-height: 20px;
}
input:hover{
    border:blue;
    border-style: solid;
    border-width: 1px;
}

button{
    background: green;
    color: white;
    height: 30px;
    font-weight: 300;
}

button:hover{
    background: firebrick;
    color: white;
}

input::-moz-selection{
    border:greenyellow;
}

input::selection{
    border:greenyellow;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#todo{
    background: #FFBD33;
    color: #9E2F17;
    border-radius: 2px;
    padding:2px;
    width:50%;
    margin-bottom:5px;
}
